import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';
import { Profile } from '../../models/profile.interface';
import { ProfileServiceProvider } from '../../providers/profile-service/profile-service';
import { LoadingServiceProvider } from '../../providers/loading-service/loading-service';
import { MasterServiceProvider } from '../../providers/master-service/master-service';
import { Master } from '../../models/master.interface';
import { TempatLahir } from '../../models/master/tempat_lahir.interface';
import { MasterWilayahServiceProvider } from '../../providers/master-service/master-wilayah-services';
import { SelectSearchableComponent } from 'ionic-select-searchable';
import { ToastServiceProvider } from '../../providers/toast-service/toast-service';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';

/**
 * Generated class for the EditProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-profile',
  templateUrl: 'edit-profile.html',
})

export class EditProfilePage {
  private token: string;
  profile: Profile;
  list_tempat_lahir: Master;
  list_title: Master;
  list_identitas: Master;
  list_agama: Master;
  list_gol_darah: Master;
  list_pendidikan: Master;
  list_pekerjaan: Master;
  list_status_kawin: Master;
  list_provinsi: Master;
  list_kabupaten: Master;
  list_kecamatan: Master;
  list_kelurahan: Master;
  list_unit_usaha: Master;
  tempat_lahir: TempatLahir;
  private requestsCount: number = 0;
  private errorsCount: number = 0;
  private loader: any;
  public style: any = {
    "background-image": "url('assets/images/avatar/default-user.png')",
    "background-size": "cover",
    "background-position": "center center",
  };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private profileService: ProfileServiceProvider,
    private auth: AuthServiceProvider,
    private loading: LoadingServiceProvider,
    private master: MasterServiceProvider,
    private wilayah: MasterWilayahServiceProvider,
    private events: Events,
    private toast: ToastServiceProvider,
    private alert: AlertServiceProvider
  ) {
    //middleware Auth 
    this.auth.AuthCheck(this.token) 
  }

  ionViewDidLoad() {

    console.log('EditProfilePage');
      this.token = this.navParams.get('token');

      if(this.token){
        this.loader  = this.loading.show('please Wait ....');
        this.loader.present();
        this.getProfile(this.token);
      }else{
        this.loader  = this.loading.show('please Wait ....');
        this.loader.present();
        if(this.events){
          this.events.subscribe('user:loged', (token, data) => {
            this.getProfile(token);
          });
        }
      }
  }

  getProfile(token: string) {
    this.profileService.profileInfo(token).subscribe(data => {this.profile = data.data, this.addon(data.data,token)}, error => this.requestError(),() => this.requestDone())
  }

  private addon(data: Profile,token: any){
    this.getListMaster(token)
    this.tempat_lahir = {
      id: data.tempat_lahir_id,
      nama: data.tempat_lahir
    }
    this.token = token;
    this.setProvinsi(this.profile.provinsi_id);
    this.profile.tempat_lahir_id = data.tempat_lahir_id;
  }

  getListMaster(token: string, ){
    this.master.get_list_tempat_lahir(token).subscribe((data) => this.list_tempat_lahir = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_agama(token).subscribe((data) => this.list_agama = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_title(token).subscribe((data) => this.list_title = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_identitas(token).subscribe((data) => this.list_identitas = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_golDarah(token).subscribe((data) => this.list_gol_darah = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_pendidikan(token).subscribe((data) => this.list_pendidikan = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_pekerjaan(token).subscribe((data) => this.list_pekerjaan = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_status_kawin(token).subscribe((data) => this.list_status_kawin = data.data, error => this.requestError(), () => this.requestDone());
    this.master.get_list_unit_usaha(token).subscribe((data) => this.list_unit_usaha = data.data, error => this.requestError(), () => this.requestDone());
    this.wilayah.get_list_provinsi(token).subscribe((data) => this.list_provinsi = data.data, error => this.requestError(), () => this.requestDone());
  }


  setProvinsi(id: number){
    this.wilayah.get_list_kabupaten(this.token, id).subscribe((data) => this.list_kabupaten = data.data, error => this.requestError(), () => {this.setKabupaten(this.profile.kabupaten_id),this.requestDone()});
  }

  setKabupaten(id: number){
    this.wilayah.get_list_kecamatan(this.token, id).subscribe((data) => this.list_kecamatan = data.data, error => this.requestError(), () => {this.setKecamatan(this.profile.kecamatan_id),this.requestDone()});
  }

  setKecamatan(id: number){
    this.wilayah.get_list_kecamatan(this.token, id).subscribe((data) => this.list_kelurahan = data.data, error => this.requestError(), () => {this.setKelurahan(this.profile.kelurahan_id),this.requestDone()});
  }

  setKelurahan(id: number) {
    console.log('master wilayah success');
  }

  private requestDone() {
    this.requestsCount++;
    console.log(this.requestsCount)
    if (this.requestsCount == 14) {
      this.loader.dismiss();
    }
  }

  private requestError() {
    this.errorsCount++;
    console.log(this.errorsCount)
    if (this.errorsCount >= 1){
      this.loader.dismiss().then(() => {
        this.toast.show('Server Busy, Please try again');
      })
    }
  }

  changeProvinsi(id: number) {
    this.profile.kabupaten_id = null;
    this.profile.kecamatan_id = null;
    this.profile.kelurahan_id = null;
    this.list_kecamatan = undefined;
    this.list_kelurahan = undefined;
    this.wilayah.get_list_kabupaten(this.token, id).subscribe((data) => this.list_kabupaten = data.data, error => console.log(error), () => console.log('changed provinsi'));
  }

  changeKabupaten(id: number) {
    this.profile.kecamatan_id = null;
    this.profile.kelurahan_id = null;
    this.list_kelurahan = undefined;
    this.profile.kelurahan_id = null;
    this.wilayah.get_list_kecamatan(this.token, id).subscribe((data) => this.list_kecamatan = data.data, error => console.log(error), () => console.log('changed kabupaten'));
  }

  changeKecamatan(id: number) {
    this.profile.kelurahan_id = null;
    this.wilayah.get_list_kecamatan(this.token, id).subscribe((data) => this.list_kelurahan = data.data, error => console.log(error), () => console.log('changed kecamatan'));
  }

  changeKelurahan(id: number) {
    console.log('master wilayah success');
  }

  changeTempatLahir(event: {
      component: SelectSearchableComponent,
      value: any 
  }) {
      console.log(event)
      this.profile.tempat_lahir_id = event.value.id;
      this.profile.tempat_lahir    = event.value.nama;
  }

  save(){
    console.log('data',this.profile)
    let loader = this.loading.show('loading ....');
    loader.present().then(() => {
      this.profileService.saveProfile(this.profile,this.token).subscribe((data) => this.saveSuccess(data,loader), error => this.saveError(error,loader), () => this.saveDone());
    })
  }

  private saveDone(){
  }

  private saveError(error,l){
    l.dismiss().then(() => {
      if (error.status == 422) {
        this.alert.presentAlert(null,this.feedbackText(error.json()),'Close');
      } else if (error.status == 400) {
        this.alert.presentAlert(null,'Bad Request','Close');
      } else {
        this.toast.show('Server Error');
      }
    });
  }

  private saveSuccess(data,l){
    l.dismiss().then(() => {
      this.navCtrl.setRoot('IndexProfilePage',{
        token: this.token,
        feedback: data.message,
      });
      //update
      this.events.publish('user:profile',this.profile);
    });
  }

  private feedbackText(error){
    let feedbackError = '<ion-list>';
    for(let key in error){
      for(let i = 0;i < error[key].length; i++){
        feedbackError += '<ion-item> - '+error[key][i]+'</ion-item><br><br>';
      }
    }
    feedbackError += '</ion-list>';

    return feedbackError;
  }

}



